header {
	border-top: solid rgb(233, 233, 233);
	border-bottom: solid rgb(233, 233, 233);
	border-width: 1px;
	margin-top: 20px;
	margin-bottom: 0px;
	display: flex;
	
	justify-content: center;
	height: 40px;
	padding: 0 2rem;
	background-color:rgb(255, 255, 255);
	color: var(--textColor);
}

nav a {
	margin: 0 1rem;

	text-decoration: none;
	color: #464646!important;
}

nav a:hover {
	color: var(--secondaryColor);
}

header .nav-btn {
	padding: 5px;
	cursor: pointer;
	background: transparent;
	border: none;
	outline: none;
	color: var(--textColor);
	visibility: hidden;
	opacity: 0;
	font-size: 1.8rem;
}

header div,
nav {
	display: flex;
	align-items: center;
}
header .nav-btn {
	display: none;
}
.logo{
	width: 200px;
	height: 200px;
}
@media only screen and (max-width: 1024px) {
	.logo{
		margin-left: 50px;
	}
	header .nav-btn {
		display: block;
		visibility: visible;
		opacity: 1;
	}
	header{
		margin-left: 0px;
	}

	header nav {
		position: fixed;
		top: -100vh;
		left: 0;
		height: 100%;
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		gap: 1.5rem;
		background-color: var(--mainColor);
		transition: 1s;
	}

	header .responsive_nav {
		transform: translateY(100vh);
	}

	nav .nav-close-btn {
		position: absolute;
		top: 2rem;
		right: 2rem;
	}

	nav a {
		font-size: 1.5rem;
	}
}