.pdCont {
    display: flex;
    justify-content: space-between;
    padding-left: 310px;
    padding-right: 310px;
    height: 1000px;
  }
  .specs{
    padding-bottom: 10px;
    padding: 10px;
 
    border: solid transparent 1px;
    border-radius: 3px;
  }
  .product-spec{

    margin-top: 40px;

    font-size: 20px;
    margin-left: 10px;
    
  }
  .specField{
    display: flex;
    align-items: center;
    justify-content: start;
    color: rgba(22, 22, 22, 0.925);
    border-bottom: solid rgba(190, 190, 190, 0.445) 1px;
    margin-left: 15px;
    margin-right: 15px;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    padding: 5px;
  }
  .product-section {
    display: flex;
  }
  
  .image-container {
    margin-right: 300px;
    margin-top: 100px;
    flex: 1;
  }
  
  .main-image {
    max-height: 500px;
    max-width: 500px;

  }
  
  .carousel-container {
    flex: 1;
  }
  
  .carousel {
    display: flex;
    flex-wrap: wrap;
  }
  
  .carousel-image {
    width: 100px;
    height: 100px;
    object-fit: cover;
    margin-left: 40px;
    margin-bottom: 10px;
  }
  
  .description-container {

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: rgb(255, 255, 255);
    flex: 1;
  }
  .specs{
    font-weight: bold;
  }
  .product-title {
    margin-top: -300px;
    color: rgb(0, 0, 0);
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  .buttonBuy{
    border: solid transparent;
    border-radius: 5px;
    padding: 8px;
    margin-top: -2px;
    font-weight: bold;
    color: white;
    background-color: rgb(204, 28, 28);
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    margin-bottom: 10px;
    cursor: pointer;
  }
  .buttonCart{
    border: solid rgba(204, 28, 28, 0.5) 1px;
    border-radius: 5px;
    padding: 8px;
    margin-top: -2px;
    font-weight: bold;
    color: rgb(204, 28, 28);
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    cursor: pointer;
  }
  .price{
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 25px;
    text-align: right;
    border-top: solid rgb(189, 189, 189) 1px;
  }
  .product-description {
    width: 400px;

    border: solid rgba(0, 0, 0, 0.144) 2px;
    border-radius: 10px;
    margin-top: 40px;
    font-size: 16px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
    padding: 10px;
  }