/* App.css */
html, body {
  max-width: 100%;
  overflow-x: hidden;
}

.wide-element {
  width: 100%;
  margin-bottom: 20px;
  padding: 10px;
  background-color: #ffffff;
  text-align: center;
  margin: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.wide-element-image {
  width: 100%;
  max-height: 300px;
  object-fit: contain;
  margin-bottom: 10px;
}

.carousel-status {
  display: none;
}

.carousel.carousel-slider .control-dots {
  display: none;
}

.wide-element-title {
  font-size: 24px;
  margin-bottom: 10px;
}

.control-arrow {
  background-color: #ff0000;
  color: #ffffff;
  font-size: 24px;
  width: 40px;
  height: 40px;
}

.searchBar {
  background-color: rgb(240, 240, 240);
  width: 550px;
  height: 50px;
  border-radius: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 2px;
}

.searchInput {
  background-color: rgb(240, 240, 240);
  border: none;
  outline: none;
  width: 480px;
  color: #8a8a8a;
}

.control-prev {
  left: 10px;
}

@media only screen and (max-width: 1024px) {
  .searchBar {
    background-color: rgb(240, 240, 240);
    width: 280px;
    height: 35px;
    border-radius: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-bottom: 2px;
  }

  .searchInput {
    background-color: rgb(240, 240, 240);
    border: none;
    outline: none;
    width: 230px;
    color: #0e0e0e;
  }

  .separator {
    margin-top: 15px;
    width: 100%;
    height: 1px;
    background-color: rgba(0, 0, 0, 0.075);
  }
}

.control-next {
  right: 10px;
}

.element {
  width: 260px;
  margin-bottom: 20px;
  margin: 5px;
  padding: 0 10px;
  border: 1px solid rgb(211, 211, 211);
  background-color: #ffffff;
  border-radius: 6px;
  text-align: center;
}

.buynadd {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.element-image {
  width: 80%;
  max-height: 200px;
  object-fit: contain;
  margin-bottom: 10px;
}

.element-title {
  color: #0e0e0e7a;
  font-size: 20px;
  margin-bottom: 10px;
}

.element-description {
  font-weight: bold;
  color: rgb(22, 22, 22);
  font-size: 14px;
  margin-bottom: 10px;
}

.container {
  height: 1000px;
  margin-left: 200px;
  margin-right: 200px;
}

.conte {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  margin-right: 17px;
}