.slider {
  margin-left: 100px;
 
  height: 482.4px;
  width: 100%;
  max-width: 1300px;
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  margin-bottom: -150px;
  .container {
    width: 100%;
    height: 60%;
    display: flex;
    transition: all 1s ease;

    img {
      width: 100%;
      height: 60%;
      object-fit: cover;
    }
  }

  .icons {
    width: fit-content;
    display: flex;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 50%;
    margin: auto;
    gap: 20px;

    .icon {
      width: 40px;
      height: 40px;
      background-color: rgba(3, 3, 3, 0.76);
      border-radius: 4px;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
    }
  }
}